import app from './_firebase';

import {getFirestore} from 'firebase/firestore'
import {getStorage} from 'firebase/storage'

export const addSlide = async (values, image) => {
  try {
    values.create_at = getFirestore().FieldValue.serverTimestamp();
    let slideRef = await app.firestore().collection('teachers').doc(values.created_by).collection('slides').add(values);
    let slide = await slideRef.get();
    const slideId = slide.id;
    let slideData = slide.data();
    if (typeof (image) == 'undefined') {
      return slideId;
    }
    var metadata = {
      contentType: 'image/jpeg'
    };
    let cover_url = 'public/images/slide/' + slide.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(cover_url).put(image, metadata);
    uploadTask.on(getStorage().TaskEvent.STATE_CHANGED, // or 'state_changed'
      function(snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case getStorage().TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case getStorage().TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function(error) {

        switch (error.code) {
          case 'storage/unauthorized':
            console.log('unauthorized');
            break;
          case 'storage/canceled':
            console.log('cancel');
            break;
          case 'storage/unknown':

            console.log('unkonwn');
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function() {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          console.log('File available at', downloadURL);
          slideData.cover = downloadURL;
          app.firestore().collection('teachers').doc(values.created_by).collection('slides').doc(slide.id).update(slideData);
        });
      });
    return slideId;
  } catch (err) {
    console.log(err);
  }
};

