export const updatePartSlide = async(subSlide)=>{
  console.log("edit  slide");
  console.log(subSlide);
  if(subSlide.type ==="master"){
    await app.firestore().collection('teachers').doc(subSlide.created_by).collection('slides').doc(subSlide.parentId).update(subSlide);
    return subSlide.id;
  }else{
    await app.firestore().collection('teachers').doc(subSlide.created_by).collection('slides').doc(subSlide.parentId).collection('subslides').doc(subSlide.id).update(subSlide);
    return subSlide.id;
  }
};

