export const getSlide = async (teacherId, slideId)=>{
  console.log(teacherId, slideId);
  let result = [];
  const slideRef = app.firestore().collection('teachers').doc(teacherId).collection('slides').doc(slideId);
  const slide = await slideRef.get();
  if (!slide.exists) {
    console.log('No such document!');
  } else {
    let slideData =  slide.data();
    slideData.id=slide.id;
    result.push(slideData);
    const subslideRef = app.firestore().collection('teachers').doc(teacherId).collection('slides').doc(slideId).collection('subslides');
    let snapshot = await subslideRef.get();
    if (snapshot.empty) {
      console.log('No matching documents.');
      return result;
    }
    snapshot.forEach(doc => {
      let data = doc.data();
      data.id=doc.id;
      result.push(data);
    });
    return result;
  }
};
export const listSlides = async (teacherId)=>{
  console.log(teacherId);
  const slideRef = app.firestore().collection('teachers').doc(teacherId).collection('slides');
  const slide = await slideRef.get();
  const listSlide = slide.docs.map((doc) => {
    return {
      id: doc.id,
      ...doc.data()
    };
  });
  return listSlide;
};


