import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listSlides } from '../../../../api/slides';
import useAuth from '../../../../hooks/useAuth';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const SlideListView = () => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [slides, setSlides] = useState([]);
  const { user } = useAuth();
  const getSlides = useCallback(async () => {
    try {
      const slides = await listSlides(user.userId);

      if (isMountedRef.current) {
        setSlides(slides);
        console.log(slides);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getSlides();}, [getSlides]);
  return (
    <Page className={classes.root} title="Giáo Viên">
      <Container maxWidth={false}>
        <Header/>
        <Box mt={3}>
          <Results slides={slides}/>
        </Box>
      </Container>
    </Page>
  );
};

export default SlideListView;
