import React, { useState } from 'react';
import { Link as RouterLink, useHistory } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  Link,
  TextField,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import FormatListNumberedIcon from '@material-ui/icons/FormatListNumbered';
import { deleteslides } from '../../../../api/slides';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'updatedAt|desc',
    label: 'Last update (newest first)'
  },
  {
    value: 'updatedAt|asc',
    label: 'Last update (oldest first)'
  },
  {
    value: 'name|desc',
    label: 'Tên (giảm)'
  },
  {
    value: 'name|asc',
    label: 'Tên (tăng)'
  }
];

const applyFilters = (slides, query, filters) => {
  return slides.filter((slide) => {
    let matches = true;

    if (query) {
      const properties = ['title', 'description'];
      let containsQuery = false;

      properties.forEach((property) => {
        if ((property in slide) && slide[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && slide[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (slides, page, limit) => {
  return slides.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (slides, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = slides.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({ slides, ...rest }) => {
  console.log(slides);
  const history = useHistory();
  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedslides, setSelectedslides] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedslides([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllslides = (event) => {
    setSelectedslides(event.target.checked
      ? slides.map((slide) => slide.id)
      : []);
  };

  const handleSelectOneslide = (event, slideId) => {
    if (!selectedslides.includes(slideId)) {
      setSelectedslides((prevSelected) => [...prevSelected, slideId]);
    } else {
      setSelectedslides((prevSelected) => prevSelected.filter((id) => id !== slideId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const handleDelete = () => {
    deleteslides(setSelectedslides);
    history.push('/teacher/management/slides');
  };

  const filteredslides = applyFilters(slides, query, filters);
  const sortedslides = applySort(filteredslides, sort);
  const paginatedslides = applyPagination(sortedslides, page, limit);
  const enableBulkOperations = selectedslides.length > 0;
  const selectedSomeslides = selectedslides.length > 0 && selectedslides.length < slides.length;
  const selectedAllslides = selectedslides.length === slides.length;

  return (
    <Card className={clsx(classes.root)}{...rest}>
      <Tabs onChange={handleTabsChange} scrollButtons="auto" textColor="secondary" value={currentTab} variant="scrollable">
        {tabs.map((tab) => (
          <Tab key={tab.value} value={tab.value} label={tab.label}/>
        ))}
      </Tabs>
      <Divider />
      <Box p={2} minHeight={56} display="flex" alignItems="center">
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon fontSize="small" color="action">
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField label="Sắp xếp" name="sort" onChange={handleSortChange} select SelectProps={{ native: true }} value={sort} variant="outlined">
          {sortOptions.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox checked={selectedAllslides} indeterminate={selectedSomeslides} onChange={handleSelectAllslides}/>
            <Button variant="outlined" className={classes.bulkAction}  onClick={handleDelete}>
              Xóa
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox checked={selectedAllslides} indeterminate={selectedSomeslides} onChange={handleSelectAllslides}/>
                </TableCell>
                <TableCell>
                  Tên
                </TableCell>
                <TableCell>
                  Mô tả
                </TableCell>
                <TableCell>
                  Tags
                </TableCell>
                <TableCell align="right">
                  Action
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedslides.map((slide) => {
                const isslideSelected = selectedslides.includes(slide.id);
                return (
                  <TableRow hover key={slide.id} selected={isslideSelected}>
                    <TableCell padding="checkbox">
                      <Checkbox checked={isslideSelected} onChange={(event) => handleSelectOneslide(event, slide.id)} value={isslideSelected}/>
                    </TableCell>
                    <TableCell>

                    {/*<Link*/}
                    {/*        color="inherit"*/}
                    {/*        component={RouterLink}*/}
                    {/*        to={"/teacher/management/slide/view/"+slide.id}*/}
                    {/*        variant="h6"*/}
                    {/*      >*/}
                    {/*        {slide.name}*/}
                    {/*      </Link>*/}
                    </TableCell>
                    <TableCell>
                      {slide.desc}
                    </TableCell>
                    <TableCell>
                      {slide.tag}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton component={RouterLink} to={"/teacher/management/slides/edit/"+ slide.id}>
                        <SvgIcon fontSize="small">
                        <EditIcon />
                        </SvgIcon>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredslides.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  slides: PropTypes.array.isRequired
};

Results.defaultProps = {
  slides: []
};

export default Results;
